import React, { Component } from "react";
import Slider from "react-slick";

export default class SimpleSlider extends Component {
  render() {
    const settings = {
      dots: false,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1
    };
    return (
      <div>
        <Slider {...settings}>
          <div className="slide1">
            <div id="text-banner">
              <h1>MY SNEKER</h1>
              <p>
                Chào mừng bạn đến với ngôi nhà Converse! Tại đây, mỗi một dòng
                chữ, mỗi chi tiết và hình ảnh đều là những bằng chứng mang dấu
                ấn lịch sử Converse 100 năm, và đang không ngừng phát triển lớn
                mạnh.
              </p>
            </div>
            <img src='./static/img/slide-1.jpg'/>
          </div>
          <div className="slide2">
            <h3>2</h3>
          </div>
          <div className="slide3">
            <h3>3</h3>
          </div>
        </Slider>
      </div>
    );
  }
}
